`, "",{"version":3,"sources":["webpack://./src/panel/component/ModelSelect.vue"],"names":[],"mappings":"AAyHA,mCACI,YAAa,CACb,qBAAsB,CACtB,UACJ,CAEA,wCACI,UACJ,CAEA,kDACI,YAAa,CACb,8BAA+B,CAC/B,UAAW,CACX,kBACJ,CAEA,wCACI,kBAAmB,CACnB,eAAgB,CAChB,sBAAuB,CACvB,gBAAiB,CACjB,cAAe,CACf,eACJ,CAEA,yBACI,kDACI,yBACJ,CACJ","sourcesContent":["<template>\n    <div class=\"mvu-model-select\">\n        <div class=\"mvu-model-select__row\">\n            <input\n                v-model=\"store.settings..\"\n                type=\"text\"\n                class=\"text_pole\"\n                autocomplete=\"off\"\n            />\n        </div>\n\n        <div class=\"mvu-model-select__row mvu-model-select__row--controls\">\n            <select\n                ref=\"select\"\n                v-model=\"selected\"\n                class=\"text_pole\"\n                :disabled=\"models.length === 0\"\n                aria-label=\"\"\n            >\n                <option value=\"\"></option>\n                <option v-for=\"model in models\" :key=\"model\" :value=\"model\">{{ model }}</option>\n            </select>\n\n            <input\n                class=\"mvu-model-select__btn menu_button menu_button_icon interactable\"\n                type=\"button\"\n                :value=\"loading ? '' : ''\"\n                :disabled=\"loading\"\n                @click=\"refresh\"\n            />\n        </div>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { useDataStore } from '@/store';\nimport { normalizeBaseURL } from '@/util';\nimport { ref, watch } from 'vue';\n\nconst store = useDataStore();\n\nconst loading = ref(false);\nconst models = ref<string[]>([]);\nconst selected = ref('');\n\nasync function refresh() {\n    if (loading.value) {\n        return;\n    }\n\n    const base_url = normalizeBaseURL(store.settings..api);\n    if (!base_url) {\n        return;\n    }\n\n    loading.value = true;\n    try {\n        const response = await fetch('/api/backends/chat-completions/status', {\n            method: 'POST',\n            headers: SillyTavern.getRequestHeaders(),\n            body: JSON.stringify({\n                reverse_proxy: base_url,\n                proxy_password: store.settings..,\n                chat_completion_source: 'openai',\n            }),\n            cache: 'no-cache',\n        });\n\n        const json = await response.json();\n\n        models.value = _(json?.data ?? [])\n            .map((model: any) => String(model?.id ?? model?.name ?? '').trim())\n            .filter(Boolean)\n            .sort()\n            .sortedUniq()\n            .value();\n        selected.value = models.value.includes(store.settings..)\n            ? store.settings..\n            : '';\n\n        if (models.value.length === 0) {\n            toastr.warning('', '[MVU]');\n        }\n    } catch (error) {\n        toastr.error(String((error as Error)?.message ?? error), '[MVU]');\n    } finally {\n        loading.value = false;\n    }\n}\n\nwatch(\n    selected,\n    value => {\n        if (!value) return;\n        store.settings.. = value;\n    },\n    { flush: 'sync' }\n);\n\nwatch(\n    () => store.settings..,\n    value => {\n        if (!value) {\n            selected.value = '';\n            return;\n        }\n        selected.value = models.value.includes(value) ? value : '';\n    },\n    { flush: 'sync' }\n);\n\nwatch(\n    () => [store.settings..api, store.settings..] as const,\n    () => {\n        models.value = [];\n        selected.value = '';\n    }\n);\n</script>\n\n<style scoped>\n.mvu-model-select {\n    display: flex;\n    flex-direction: column;\n    gap: 0.5rem;\n}\n\n.mvu-model-select__row {\n    width: 100%;\n}\n\n.mvu-model-select__row--controls {\n    display: grid;\n    grid-template-columns: 1fr auto;\n    gap: 0.5rem;\n    align-items: center;\n}\n\n.mvu-model-select__btn {\n    white-space: nowrap;\n    text-align: left;\n    padding: 0.35rem 0.6rem;\n    min-height: unset;\n    height: 2.05rem;\n    line-height: 1.1;\n}\n\n@media (max-width: 520px) {\n    .mvu-model-select__row--controls {\n        grid-template-columns: 1fr;\n    }\n}\n</style>\n"],"sourceRoot":""}]);
