`, "",{"version":3,"sources":["webpack://./src/panel/component/RangeNumber.vue"],"names":[],"mappings":"AA2CA,mCACI,YAAa,CACb,gCAAiC,CACjC,UAAW,CACX,kBACJ,CAEA,0CACI,UACJ,CAEA,2CACI,eAAgB,CAChB,kBAAmB,CACnB,qBAAsB,CACtB,mGAKJ,CAEA,yBACI,mCACI,gCACJ,CACJ","sourcesContent":["<template>\n    <div class=\"mvu-range-number\">\n        <template v-for=\"type in ['range', 'number']\" :key=\"type\">\n            <input\n                :class=\"[\n                    `mvu-range-number__${type}`,\n                    type === 'number' ? 'text_pole' : '',\n                ]\"\n                :type=\"type\"\n                :min=\"min\"\n                :max=\"max\"\n                :step=\"step\"\n                :disabled=\"disabled\"\n                :value=\"model\"\n                @input=\"onInput\"\n            />\n        </template>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nconst model = defineModel<number>({ required: true });\nconst props = defineProps<{\n    min: number;\n    max: number;\n    step: number;\n    disabled?: boolean;\n}>();\n\nfunction clamp(value: number) {\n    return _.clamp(value, props.min, props.max);\n}\n\nfunction onInput(event: Event) {\n    const target = event.target as HTMLInputElement | null;\n    const value = Number(target?.value);\n    if (Number.isFinite(value)) {\n        model.value = clamp(value);\n    }\n}\n</script>\n\n<style scoped>\n.mvu-range-number {\n    display: grid;\n    grid-template-columns: 1fr 7.5rem;\n    gap: 0.5rem;\n    align-items: center;\n}\n\n.mvu-range-number__range {\n    width: 100%;\n}\n\n.mvu-range-number__number {\n    text-align: left;\n    padding-top: 0.3rem;\n    padding-bottom: 0.3rem;\n    background-color: color-mix(\n        in srgb,\n        var(--SmartThemeBlurTintColor, rgba(31, 31, 31, 1)) 33%,\n        transparent\n    );\n}\n\n@media (max-width: 420px) {\n    .mvu-range-number {\n        grid-template-columns: 1fr 6.5rem;\n    }\n}\n</style>\n"],"sourceRoot":""}]);
